/**
 * 右侧内容-教学工具箱 Smart组件
 * @module dialTeachingToolComponent
 * @description   转盘组件
 * @author liujianhang
 * @date 2017/09/20
 */
'use strict';
import React from 'react';
import TkGlobal from 'TkGlobal' ;
import TkConstant from 'TkConstant' ;
import eventObjectDefine from 'eventObjectDefine';
import ServiceSignalling from 'ServiceSignalling' ;
import TkUtils from 'TkUtils';
import Draggable,{DraggableCore} from 'react-draggable';

class DialTeachingToolSmart extends React.Component {
    constructor(props){
        super(props);
        let {id} = this.props;
        this.state={
            dialTeachComponentBgDisplay:"none",
            dialIconClose:false,
            turnTableDeg:'rotate(0)',
            dialShow:false,
            deg:45,
            numdeg:0,
            num:0,
            isClick:false,
            [id]:{
                left:4.4,
                top:0,
                offsetX:0,
                offsetY:0,
            },
            msgDeg:false
        };
        this[id] = {};//保存位置的百分比
        this.isHasTransition = false;
        this.listernerBackupid =  new Date().getTime()+'_'+Math.random();

    };
    componentDidMount() { //在完成首次渲染之前调用，此时仍可以修改组件的state
        const that = this ;
        eventObjectDefine.Window.addEventListener(TkConstant.EVENTTYPE.WindowEvent.onResize , that.handlerOnResize.bind(that)   , that.listernerBackupid); //window.resize事件
 // 	eventObjectDefine.CoreController.addEventListener("initAppPermissions", that.handlerInitAppPermissions.bind(that), that.listernerBackupid);
        eventObjectDefine.CoreController.addEventListener( "receive-msglist-dial" , that.handlerMsglistDialShow.bind(that), that.listernerBackupid);
      //	eventObjectDefine.CoreController.addEventListener(rename.EVENTTYPE.RoomEvent.roomParticipantJoin, that.handlerRoomParticipantJoin.bind(that) , that.listernerBackupid); //监听用户加入
    //	eventObjectDefine.CoreController.addEventListener(rename.EVENTTYPE.RoomEvent.roomParticipantLeave, that.handlerRoomParticipantLeave.bind(that) , that.listernerBackupid); //监听用户离开
        eventObjectDefine.CoreController.addEventListener(TkConstant.EVENTTYPE.RoomEvent.roomPubmsg ,that.handlerRoomPubmsg.bind(that)  ,  that.listernerBackupid ) ;//room-pubmsg事件
        eventObjectDefine.CoreController.addEventListener( TkConstant.EVENTTYPE.RoomEvent.roomDelmsg , that.handlerRoomDelmsg.bind(that), that.listernerBackupid); //roomDelmsg事件
        eventObjectDefine.CoreController.addEventListener(TkConstant.EVENTTYPE.RoomEvent.roomUserpropertyChanged ,that.handlerroomUserpropertyChanged.bind(that) , that.listernerBackupid ); //roomUserpropertyChanged事件
        eventObjectDefine.CoreController.addEventListener(TkConstant.EVENTTYPE.RoomEvent.roomPlaybackClearAllFromPlaybackController,that.handlerRoomPlaybackClearAll.bind(that) , that.listernerBackupid); //roomPlaybackClearAll 事件：回放清除所有信令
        eventObjectDefine.CoreController.addEventListener( "receive-msglist-DialDrag" , that.handlerMsglistDialDrag.bind(that), that.listernerBackupid);//msglist,后面进来的人收到小黑板的拖拽位置

    };
    componentWillUnmount() { //组件被移除之前被调用，可以用于做一些清理工作，在componentDidMount方法中添加的所有任务都需要在该方法中撤销，比如创建的定时器或添加的事件监听器
        let that = this ;
        eventObjectDefine.Window.removeBackupListerner(that.listernerBackupid);
        eventObjectDefine.CoreController.removeBackupListerner(that.listernerBackupid);
    };
    handlerOnResize() {
        let that = this;
        let {id} = this.props;
        if (Object.keys(that[id]).length !== 0) {
            let {percentLeft,percentTop} = that[id];
            TkUtils.percentageChangeToRem(that,percentLeft,percentTop,id);
            this.setState({[id]:this.state[id]});
        }
    };
    componentDidUpdate() {//每次render结束后会触发,每次转过之后去除过度效果
        if (this.isHasTransition == true) {
            this.isHasTransition = false;
        }
    };
    handlerroomUserpropertyChanged(roomUserpropertyChangedEventData){
        const that = this ;
        const changePropertyJson  = roomUserpropertyChangedEventData.message ;
        const user  = roomUserpropertyChangedEventData.user ;
        for( let key of Object.keys(changePropertyJson) ){
            if( key === 'publishstate' ){
//	                that.goStageUsers();!!!!
            }
        }
    };
   
    handlerMsglistDialShow(recvEventData){
        const that = this;
        this.state.msgDeg = true;
        this.setState({msgDeg:this.state.msgDeg})
        for(let message of recvEventData.message.dialShowArr){
            if(TkConstant.hasRole.roleChairman || TkConstant.hasRole.roleTeachingAssistant||TkConstant.hasRole.rolePatrol){ 
		        if (message.data.isShow) {
		            that.state.turnTableDeg = 'rotate(0)';
		            that.state.dialTeachComponentBgDisplay = "block";
		            that.state.dialIconClose = true;
		            that.setState({
		                dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay,
		                turnTableDeg: that.state.turnTableDeg,
		                dialIconClose: that.state.dialIconClose
		            });
		        }
		        else {
		            that.state.dialTeachComponentBgDisplay = "block";
		            that.state.turnTableDeg = message.data.rotationAngle;
		            that.state.dialIconClose = true;
		            that.setState({
		                dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay,
		                turnTableDeg: that.state.turnTableDeg,
		                dialIconClose: that.state.dialIconClose,
		                isClick: that.state.isClick,
		            });
		        }
		    }else{
		    	if (message.data.isShow) {
		            that.state.turnTableDeg = 'rotate(0)';
		            that.state.dialIconClose = false;
		            that.state.dialTeachComponentBgDisplay = "block";
		            that.setState({
		                dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay,
		                turnTableDeg: that.state.turnTableDeg,
		                dialIconClose: that.state.dialIconClose
		            });
		        }
		        else {
		            that.state.dialTeachComponentBgDisplay = "block";
		            that.state.turnTableDeg = message.data.rotationAngle;
		            that.state.dialIconClose = false;
		            that.setState({
		                dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay,
		                turnTableDeg: that.state.turnTableDeg,
		                dialIconClose: that.state.dialIconClose
		            });
		        }
		    }
        }
    };
    handlerMsglistDialDrag(handleData) {//msglist,后面进来的人收到转盘的拖拽位置
        let DialDragInfo = handleData.message.DialDragArray;
        const that = this;
        let {id} = that.props;
        DialDragInfo.map((item,index)=>{
            let {percentLeft,percentTop}  = item.data;
            this[id] = {
                percentLeft:percentLeft,
                percentTop:percentTop,
            };
            TkUtils.percentageChangeToRem(that,percentLeft,percentTop,id);
            that.setState({[id]:that.state[id]});
        });
    };
    handlerRoomPubmsg(recvEventData){
        const that = this;
        let pubmsgData = recvEventData.message;
        switch (pubmsgData.name) {
            case "dial":
            
                that.handlerDisplayTeachTool(pubmsgData);
                break;
            case 'DialDrag'://转盘的拖拽
                let {id} = that.props;
                this.isHasTransition = true;
                let {percentLeft,percentTop}  = pubmsgData.data;
                this[id] = {
                    percentLeft:percentLeft,
                    percentTop:percentTop,
                };
                TkUtils.percentageChangeToRem(that,percentLeft,percentTop,id);
                this.setState({[id]:this.state[id]});
                break;
        }
    }
    handlerRoomDelmsg(recvEventData){
        const that = this;
        let pubmsgData = recvEventData.message;
        switch (pubmsgData.name) {
            case "dial":
                let {id} = this.props;
                that.state.dialTeachComponentBgDisplay = "none";
                that.state.num = 0;
                that.state.numdeg = 0;
                that.state.deg = 45;
                that.state[id].left = 4.4;
                that.state[id].top = 0;
                that.setState({
                    dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay,
                    num:that.state.num,
                    numdeg:that.state.numdeg,
                    deg:that.state.deg,
                    [id]:that.state[id],
                });
                break;
            case "ClassBegin":
                that.state.dialTeachComponentBgDisplay = "none";
                that.setState({
                    dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay
                });
                break;
        }
    }
    handlerDisplayTeachTool(recvEventData){
        const that = this;
       	if(TkConstant.hasRole.roleChairman || TkConstant.hasRole.roleTeachingAssistant||TkConstant.hasRole.rolePatrol){ 
	        if (recvEventData.data.isShow) {
	            that.state.dialTeachComponentBgDisplay = "block";
	            that.state.dialIconClose = true;
	            that.setState({
	                dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay,
	                turnTableDeg: that.state.turnTableDeg,
	                dialIconClose: that.state.dialIconClose
	            });
	        }
	        else {
	            that.state.dialTeachComponentBgDisplay = "block";
	            that.state.turnTableDeg = recvEventData.data.rotationAngle;
	            that.state.dialIconClose = false;
	            that.state.msgDeg = false;
	            that.setState({
	                dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay,
	                turnTableDeg: that.state.turnTableDeg,
	                dialIconClose: that.state.dialIconClose,
	                msgDeg:that.state.msgDeg
	            });
	            setTimeout(function () {
	                that.state.dialIconClose = true;
	                that.state.isClick = false;
	                that.setState({
	                    dialIconClose: that.state.dialIconClose,
	                    isClick:that.state.isClick,
	                });
	            }, 4000)
	        }
	    }else{
	    	if (recvEventData.data.isShow) {
	            that.state.turnTableDeg = 'rotate(0)';
	            that.state.dialIconClose = false;
	            that.state.dialTeachComponentBgDisplay = "block";
	            that.setState({
	                dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay,
	                turnTableDeg: that.state.turnTableDeg,
	                dialIconClose: that.state.dialIconClose
	            });
	        }
	        else {
	            that.state.dialTeachComponentBgDisplay = "block";
	            that.state.turnTableDeg = recvEventData.data.rotationAngle;
	            that.state.dialIconClose = false;
	            that.setState({
	                dialTeachComponentBgDisplay: that.state.dialTeachComponentBgDisplay,
	                turnTableDeg: that.state.turnTableDeg,
	                dialIconClose: that.state.dialIconClose
	            });
	        }
	    }
    };

    handlerRoomPlaybackClearAll(){
        if(!TkGlobal.playback){L.Logger.error('No playback environment, no execution event[roomPlaybackClearAll] handler ') ;return ;};
        const that = this ;
        that.setState({
            dialTeachComponentBgDisplay:"none",
            dialIconClose:false,
            turnTableDeg:'rotate(0)',
            dialShow:false
        });
    };

    /*指针的点击函数*/
    opointer(e){
        if (TkConstant.hasRole.roleStudent || this.state.isClick === true) {
            return false;
        }
        if(this.state.isClick){
            return false
        }
        if(this.state.msgDeg){
			this.state.turnTableDeg = 'rotate(0)';
		}
		this.state.msgDeg = false;
        this.state.isClick = true;
        this.state.dialShow =false;
        let index = Math.floor(Math.random() * 5+1); //得到0-7随机数
        this.state.num = index + this.state.num; //得到本次位置
        this.state.numdeg += index * this.state.deg + Math.floor(Math.random() * 2 + 3) * 360;		
        this.setState({
            dialShow: this.state.dialShow,
            num:this.state.num,
            numdeg:this.state.numdeg,
            deg:this.state.deg,
            isClick:this.state.isClick,
            msgDeg:this.state.msgDeg
        });
        let iconShow=this.state.dialShow
        let data={
                rotationAngle:'rotate('+ this.state.numdeg +'deg)',
                isShow:iconShow

            };
        ServiceSignalling.sendSignallingDialToStudent(data);
    };
    /*关闭指针*/
    dialClosePHandle(){
        // ReactDOM.findDOMNode(this.refs.turnTable).style.transform=0 ;
        this.state.num = 0;
        this.state.numdeg = 0;
        this.state.deg = 45;
        this.state.turnTableDeg = 'rotate(0)';
        this.state.dialTeachComponentBgDisplay='none';
        this.setState({
            dialTeachComponentBgDisplay:this.state.dialTeachComponentBgDisplay,
            turnTableDeg: this.state.turnTableDeg,
            num:this.state.num,
            numdeg:this.state.numdeg,
            deg:this.state.deg
        });
        let data={
            dialClose:'none'
        };
        let isDelMsg=true;
        ServiceSignalling.sendSignallingDialToStudent(data , isDelMsg);
        this.state.studentNameArr=[];
        this.setState({studentNameArr:this.state.studentNameArr});
        //初始化拖拽元素的位置
        let {id} = this.props;
        this.state[id].left = 4.4;
        this.state[id].top = 0;
        this.setState({[id]:this.state[id]});
    };

    handlerOnStartDrag(e,dragData) {//开始拖拽
        let that = this;
        let {id} = that.props;
        const defalutFontSize = window.innerWidth / TkConstant.STANDARDSIZE ;
        that.state[id].offsetX = dragData.x/defalutFontSize - that.state[id].left;
        that.state[id].offsetY = dragData.y/defalutFontSize - that.state[id].top;
    };
    handlerOnDragging(e,dragData) {//拖拽中
        let that = this;
        let {id} = that.props;
        const defalutFontSize = window.innerWidth / TkConstant.STANDARDSIZE ;
        // 触摸点相对白板区的位置-触摸点相对触摸元素边界的位置：
        that.state[id].left = dragData.x/defalutFontSize - that.state[id].offsetX;
        that.state[id].top = dragData.y/defalutFontSize - that.state[id].offsetY;
        let dragEleId = id;//拖拽的元素
        let boundsEleId = 'tk_app';//控制拖拽边界的元素
        that.state[id] = TkUtils.controlDragBounds(that,dragEleId,boundsEleId);
        that.setState({[id]:that.state[id]});
    };
    handlerOnStopDrag(e,dragData) {//拖拽结束
        let that = this;
        let {id} = that.props;
        that.state[id].offsetX = 0;
        that.state[id].offsetY = 0;
        if (TkConstant.hasRole.roleChairman || TkConstant.hasRole.roleTeachingAssistant) {
            let {left,top} = that.state[id];
            let {percentLeft,percentTop}  = TkUtils.RemChangeToPercentage(that,left,top,id);
            that[id] = {
                percentLeft:percentLeft,
                percentTop:percentTop,
            };
            let data = {
                percentLeft:that[id].percentLeft,
                percentTop:that[id].percentTop,
                isDrag:true,
            };
            ServiceSignalling.sendSignallingFromDialDrag(data);
        }
    };
    render(){
        let that = this;
        let {studentNameArr} = this.state ;
        const {getItem,id} = this.props;

        let draggableData = {
            onStart: that.handlerOnStartDrag.bind(that),//开始拖拽
            onDrag: that.handlerOnDragging.bind(that),//拖拽中
            onStop: that.handlerOnStopDrag.bind(that),//拖拽结束
        };
        let dialDragStyle = {
            position:'absolute',
            zIndex:110,
            display:this.state.dialTeachComponentBgDisplay,
            width:TkConstant.hasRole.roleStudent?'2rem':'2.2rem',
            height:TkConstant.hasRole.roleStudent?'2rem':'2.4rem',
            transitionProperty:this.isHasTransition?'left,top':undefined,
            transitionDuration:this.isHasTransition?'0.4s':undefined,
            left:this.state[id].left+'rem',
            top:this.state[id].top+'rem',
        };
        return (
            <DraggableCore {...draggableData}>
                <div id="dialDrag" style={dialDragStyle}>
                    <div className="dial-teachComponent-bg" ref="dialTeachComponentBg"  style={{display:this.state.dialTeachComponentBgDisplay,top:TkConstant.hasRole.roleStudent?'0':'0.4rem'}}>
                        <button alt="pointer" style={{backgroundColor:this.state.a}} className="dial-teachComponent-pointer-button" onTouchStart={this.opointer.bind(this)} ></button>
                        <div className="dial-teachComponent-turntable" ref='turnTable' style={{transition: 'transform 4s ease',transform:this.state.turnTableDeg}}>
                        </div>
                        {/*<div style={{height:'100%',zIndex:121}}></div>*/}
                        <div className="dialCloseP" onTouchStart={this.dialClosePHandle.bind(this)} style={{display:this.state.dialIconClose?"block":"none"}}></div>
                    </div>
                </div>
            </DraggableCore>
        )
    }
}

export default DialTeachingToolSmart;


