/**
 * 右侧内容-白板标注工具以及全员操作的Smart组件
 * @module WhiteboardToolAndControlOverallBarSmart
 * @description   承载右侧内容-白板标注工具以及全员操作的所有Smart组件
 * @author QiuShao
 * @date 2017/08/14
 */
'use strict';
import React from 'react';
import ControlOverallBarSmart from './controlOverallBar';
import WhiteboardToolBarSmart from './whiteboardToolBar';
import eventObjectDefine from 'eventObjectDefine';
import TkUtils from 'TkUtils' ;
import TkGlobal from 'TkGlobal' ;
import Draggable from 'react-draggable';

class WhiteboardToolAndControlOverallBarSmart extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            lc_tool_container:{
                pagingToolLeft:"",
                pagingToolTop:"",
            },
            updateState:false ,
        };
        this.listernerBackupid = new Date().getTime()+'_'+Math.random();
    };

    componentDidMount() { //在完成首次渲染之前调用，此时仍可以修改组件的state
        eventObjectDefine.CoreController.addEventListener(TkConstant.EVENTTYPE.RoomEvent.roomConnected , this.handlerRoomConnected.bind(this) , this.listernerBackupid  ); //roomConnected事件
    };
    componentWillUnmount() { //组件被移除之前被调用，可以用于做一些清理工作，在componentDidMount方法中添加的所有任务都需要在该方法中撤销，比如创建的定时器或添加的事件监听器
        let that = this ;
        eventObjectDefine.CoreController.removeBackupListerner(that.listernerBackupid);
    };

    handlerRoomConnected(){
        this.setState({updateState:!this.state.updateState});
    }

    render(){
        let that = this ;
        return (
            <Draggable bounds="#tk_app">
                <div id="lc_tool_container" className={"lc-tool-container clear-float tk-tool-right "+(TkGlobal.mobileDeviceType === 'phone' ? 'phone ':'pc pad ' ) } style={{position:"absolute"}} >{/*白板工具栏以及全体操作功能栏*/}
                    { TkGlobal.mobileDeviceType === 'phone' ? undefined: <ControlOverallBarSmart parentId={"lc_tool_container"} />}{/*教学工具箱*/}
                    <WhiteboardToolBarSmart parentId={"lc_tool_container"}  />
                </div>
            </Draggable>
        )
    };
};
export default  WhiteboardToolAndControlOverallBarSmart;




