/**
 * 主要入口
 * @module tkApp
 * @description   提供 系统程序的入口
 * @author QiuShao
 * @date 2017/7/21
 */

'use strict';
import React  from 'react';
import ReactDom from 'react-dom';
import { Router, Route, hashHistory } from 'react-router';
import CoreController from  'CoreController' ;
import TkMobile from './containers/tkMobile/tk-mobile';

CoreController.handler.loadSystemRequiredInfo(); //加载系统所需的信息
/*根据路由决定渲染的页面*/
ReactDom.render((
    <Router history={hashHistory}  >
        <Route path='/' component={TkMobile}></Route>
        <Route path='/mobile' component={TkMobile} />
        <Route path='/mobileApp' component={TkMobile} />
    </Router>
), document.getElementById('tk_app'));






