/**
 * 右侧内容-教学工具箱 Smart组件
 * @module responderStudentToolComponent
 * @description   抢答器组件
 * @author liujianhang
 * @date 2017/10/30
 */
'use strict';
import React from 'react';
import ReactDOM from 'react-dom';
import TkGlobal from 'TkGlobal' ;
import rename from 'TkConstant';
import TkConstant from 'TkConstant' ;
import TkUtils from 'TkUtils';
import eventObjectDefine from 'eventObjectDefine';
import CoreController from 'CoreController';
import ServiceRoom from 'ServiceRoom';
import ServiceSignalling from 'ServiceSignalling' ;
import Draggable,{DraggableCore} from 'react-draggable';

class ResponderStudentToolSmart extends React.Component {
	constructor(props){
		super(props);
        let {id} = this.props;
		this.state={
			responderStudentIsShow:"none",
			responderTop:"",
			responderLeft:"",
			timesRun:3,
			studentText:"",
			userName:null,
			isClick:false,
			userSort:{},
			userArry:[],
			translateMove:null,
            [id]:{
                left:4.4,
                top:0,
                offsetX:0,
                offsetY:0,
            },
		};
		this.intervals = undefined;
		this.timeOutIntervals = undefined;
        this.isHasTransition = false;
        this[id] = {};//保存位置的百分比
		this.listernerBackupid =  new Date().getTime()+'_'+Math.random();
	};
	componentDidMount() { //在完成首次渲染之前调用，此时仍可以修改组件的state
		const that = this;
        eventObjectDefine.Window.addEventListener(TkConstant.EVENTTYPE.WindowEvent.onResize , that.handlerOnResize.bind(that)   , that.listernerBackupid); //window.resize事件
        //eventObjectDefine.CoreController.addEventListener( 'changeOtherVideoStyle' , that.handleChangeOtherVideoStyle.bind(that) , that.listernerBackupid);
		eventObjectDefine.CoreController.addEventListener( "receive-msglist-qiangDaQi" , that.handlerMsglistQiangDaQi.bind(that), that.listernerBackupid);
		eventObjectDefine.CoreController.addEventListener( "receive-msglist-QiangDaZhe" , that.handlerMsglistQiangDaZhe.bind(that), that.listernerBackupid);
        eventObjectDefine.CoreController.addEventListener( TkConstant.EVENTTYPE.RoomEvent.roomPubmsg , that.handlerRoomPubmsg.bind(that), that.listernerBackupid);
        eventObjectDefine.CoreController.addEventListener( TkConstant.EVENTTYPE.RoomEvent.roomDelmsg , that.handlerRoomDelmsg.bind(that), that.listernerBackupid); //roomDelmsg事件
    };
    componentWillUnmount() { //组件被移除之前被调用，可以用于做一些清理工作，在componentDidMount方法中添加的所有任务都需要在该方法中撤销，比如创建的定时器或添加的事件监听器
        let that = this ;
        eventObjectDefine.Window.removeBackupListerner(that.listernerBackupid);
        eventObjectDefine.CoreController.removeBackupListerner(that.listernerBackupid);
    };
    handlerOnResize() {
        let that = this;
        let {id} = this.props;
        if (Object.keys(that[id]).length !== 0) {
            let {percentLeft,percentTop} = that[id];
            TkUtils.percentageChangeToRem(that,percentLeft,percentTop,id);
            this.setState({[id]:this.state[id]});
        }
    };
    componentDidUpdate() {//每次render结束后会触发,每次转过之后去除过度效果
        if (this.isHasTransition === true) {
            this.isHasTransition = false;
        }
    };
    handlerRoomPubmsg(recvEventData){
        const that = this;
        let pubmsgData = recvEventData.message;
        let users =ServiceRoom.getTkRoom().getMySelf();
        switch (pubmsgData.name) {
            case "qiangDaQi":
                if(TkConstant.hasRole.roleStudent && users.publishstate>=0){
                    that._teacherRecevedServiceQiangDaQiData(pubmsgData);
                }
            	break;
            case "QiangDaZhe":
                if(TkConstant.hasRole.roleStudent){
                    that._teacherRecevedServiceQiangDaZheData(pubmsgData);
                }
            	break;
            case 'ResponderDrag'://抢答器的拖拽
                let {id} = that.props;
                this.isHasTransition = true;
                let {percentLeft,percentTop}  = pubmsgData.data;
                this[id] = {
                    percentLeft:percentLeft,
                    percentTop:percentTop,
                };
                TkUtils.percentageChangeToRem(that,percentLeft,percentTop,id);
                this.setState({[id]:this.state[id]});
                break;
        }
    };
    handlerRoomDelmsg(recvEventData){
        const that = this;
        let pubmsgData = recvEventData.message;
        switch (pubmsgData.name) {
            case "ClassBegin":
                that.state.responderStudentIsShow = "none";
				that.setState({responderStudentIsShow:that.state.responderStudentIsShow})
                break;
            case "qiangDaQi":
				if (pubmsgData.data.isShow === false){
                    //初始化拖拽元素的位置
                    let {id} = this.props;
                    this.state[id].left = 4.4;
                    this.state[id].top = 0;
                     }
                    that.state.responderStudentIsShow = "none";
                    that.state.isClick = false;
                    that.state.timesRun = 3;
                    that.state.userArry = [];
                    that.state.userSort = {};
                    that.state.studentText = that.state.timesRun;
                    that.setState({
                        [id]:this.state[id],
                        userArry: that.state.userArry,
                        userSort: that.state.userSort,
                        responderStudentIsShow: that.state.responderStudentIsShow,
                        isClick: that.state.isClick,
                        timesRun: that.state.timesRun,
                        studentText: that.state.studentText
                    })
            	break;
        }
    };
     handlerMsglistQiangDaQi(recvEventData){
     	const that = this;
     	let users =ServiceRoom.getTkRoom().getMySelf();
    	for(let message of recvEventData.message.qiangDaQiArr){
    		 if(TkConstant.hasRole.roleStudent && users.publishstate>0){
            	that._teacherRecevedServiceQiangDaQiData(message);
            }
    	}
    };
    handlerMsglistQiangDaZhe(recvEventData){
    	const that = this;
    	for(let message of recvEventData.message.QiangDaZheArr){
    		 if(TkConstant.hasRole.roleStudent){
            	that._teacherRecevedServiceQiangDaZheData(message);
            }
    	}
    };
    _teacherRecevedServiceQiangDaQiData(pubmsgData){
    	const that = this;
    	let {id} = that.props;
    	let serviceTimeData = TkGlobal.serviceTime / 1000 - pubmsgData.ts;
		if(pubmsgData.data.begin){
    		that.state.responderStudentIsShow="block";
    		if(serviceTimeData>=8){
				that.state.studentText = TkGlobal.language.languageData.responder.noContest.text;
                if (Object.keys(that[id]).length !== 0) {
                    let {percentLeft, percentTop} = that[id];
                    TkUtils.percentageChangeToRem(that, percentLeft, percentTop, id);
                }else {
                    that.state[id].left = 4.4;
                    that.state[id].top = 0;
                }
				that.setState({
					studentText: that.state.studentText,
                    [id]:this.state[id],
				});
    		}else{
                that.intervals = setInterval(function () {
                    that.state.timesRun -= 1;
                    if (that.state.timesRun === 0) {
                        TkUtils.percentageChangeToRem(that, 0.2, 0.2, id);
                    }else if (that.state.timesRun === 1) {
                        TkUtils.percentageChangeToRem(that, 0.4, 0.6, id);
                    }else if (that.state.timesRun === 2) {
                        TkUtils.percentageChangeToRem(that, 0.7, 0.2, id);
                    }
                    that.state.studentText = that.state.timesRun;
                    that.setState({
                        [id]:that.state[id],
                        timesRun: that.state.timesRun,
                        studentText: that.state.studentText
                    });
                    if (that.state.timesRun <= 0) {
                        clearInterval(that.intervals);
                        that.state.studentText = TkGlobal.language.languageData.responder.answer.text;
                        that.setState({
                            studentText: that.state.studentText
                        });
                        if (that.state.isClick == false) {
                            that.timeOutIntervals = setTimeout(function () {
                                that.isHasTransition = true;
                                that.state.studentText = TkGlobal.language.languageData.responder.noContest.text;
                                if (Object.keys(that[id]).length !== 0) {
                                    let {percentLeft, percentTop} = that[id];
                                    TkUtils.percentageChangeToRem(that, percentLeft, percentTop, id);
                                }else {
                                    that.state[id].left = 4.4;
                                    that.state[id].top = 0;
                                }
                                that.setState({
                                    studentText: that.state.studentText,
                                    [id]:that.state[id],
                                });
                            }, 4500)
                        }
                    }
                }, 1000);
            }
            that.setState({responderStudentIsShow: that.state.responderStudentIsShow,})
        } else {
            if (pubmsgData.data.isShow) {
                that.state.timesRun = 3;
                that.state.responderStudentIsShow = "none";
                that.state.studentText = 3;
                that.setState({
                    timesRun: that.state.timesRun,
                    responderStudentIsShow: that.state.responderStudentIsShow,
                    studentText: that.state.studentText
                })
            }
    	}
    };
    _teacherRecevedServiceQiangDaZheData(pubmsgData){
        const that = this;
        let {id} = that.props;
        if (pubmsgData.data.isClick) {
            that.state.userSort[pubmsgData.fromID] = {};
            that.state.userSort[pubmsgData.fromID][pubmsgData.seq] = pubmsgData.data.userAdmin;
            that.setState({userSort: that.state.userSort});
            that.state.userArry = [];
            for (let item in that.state.userSort) {
                for (let i in that.state.userSort[item]) {
                    that.state.userArry.push(i);
                    that.state.userArry = that.state.userArry.sort()
                    that.setState({userArry: that.state.userArry});
                    if (that.state.userSort[item][that.state.userArry[0]] != undefined) {
                        that.state.studentText = that.state.userSort[item][that.state.userArry[0]];
                        that.setState({studentText: that.state.studentText, firstUser: that.state.firstUser});
                    }
                }
            }
            clearInterval(that.intervals);
            clearTimeout(that.timeOutIntervals);
            if (Object.keys(that[id]).length !== 0) {
                let {percentLeft, percentTop} = that[id];
                TkUtils.percentageChangeToRem(that, percentLeft, percentTop, id);
            }else {
                that.state[id].left = 4.4;
                that.state[id].top = 0;
            }
            that.isHasTransition = true;
            that.setState({
                [id]:this.state[id],
                studentText: that.state.studentText,
            });
        }
    };
    /*抢答*/
    studentResponder() {
        let users = ServiceRoom.getTkRoom().getMySelf();
        if (users.publishstate>=0 && TkConstant.hasRole.roleStudent && this.state.studentText == TkGlobal.language.languageData.responder.answer.text && this.state.isClick == false) { //tkpc2.0.8
            this.state.userAdmin = users.nickname;
            this.setState({userAdmin: this.state.userAdmin})
            this.state.isClick = true;
            this.setState({isClick: this.state.isClick});
            let userAdmin = this.state.userAdmin;
            let isClick = this.state.isClick
            let data = {
                userAdmin: userAdmin,
                isClick: isClick
            };
            let isDelMsg = false;
            ServiceSignalling.sendSignallingQiangDaZhe(isDelMsg, data);
        }
    };
    handlerOnStartDrag(e,dragData) {//开始拖拽
        let that = this;
        let {id} = that.props;
        const defalutFontSize = window.innerWidth / TkConstant.STANDARDSIZE ;
        that.state[id].offsetX = dragData.x/defalutFontSize - that.state[id].left;
        that.state[id].offsetY = dragData.y/defalutFontSize - that.state[id].top;
    };
    handlerOnDragging(e,dragData) {//拖拽中
        let that = this;
        let {id} = that.props;
        const defalutFontSize = window.innerWidth / TkConstant.STANDARDSIZE ;
        // 触摸点相对白板区的位置-触摸点相对触摸元素边界的位置：
        that.state[id].left = dragData.x/defalutFontSize - that.state[id].offsetX;
        that.state[id].top = dragData.y/defalutFontSize - that.state[id].offsetY;
        let dragEleId = id;//拖拽的元素
        let boundsEleId = 'tk_app';//控制拖拽边界的元素
        that.state[id] = TkUtils.controlDragBounds(that,dragEleId,boundsEleId);
        that.setState({[id]:that.state[id]});
    };
    handlerOnStopDrag(e,dragData) {//拖拽结束
        let that = this;
        let {id} = that.props;
        that.state[id].offsetX = 0;
        that.state[id].offsetY = 0;
    };
	render(){
	    let that = this;
	    let {id} = that.props;
        let draggableData = {
            onStart: that.handlerOnStartDrag.bind(that),//开始拖拽
            onDrag: that.handlerOnDragging.bind(that),//拖拽中
            onStop: that.handlerOnStopDrag.bind(that),//拖拽结束
        };
        let responderDragStyle = {
            display: this.state.responderStudentIsShow,
            left:this.state[id].left+'rem',
            top:this.state[id].top+'rem',
            transitionProperty:this.isHasTransition?'left,top':undefined,
            transitionDuration:this.isHasTransition?'0.5s':undefined,
        };
        return (
            <DraggableCore {...draggableData}>
                <div className="responder-circle-student" id={id} style={responderDragStyle}>
                    <div className="responder-black-circle-student"></div>
                    <div className={"responder-begin-circle-student"+" "+(typeof(this.state.studentText)=="number"?'disabled':'')} onTouchStart={this.studentResponder.bind(this)}>
                        {this.state.studentText}
                    </div>
                </div>
            </DraggableCore>
        )
    }
}

export default ResponderStudentToolSmart;


