/**
 * 右侧内容-全员操作功能 Smart组件
 * @module ControlOverallBarSmart
 * @description   承载右侧内容-全员操作的所有Smart组件
 * @author QiuShao
 * @date 2017/08/14
 */

'use strict';
import React from 'react';
import TkGlobal from 'TkGlobal' ;
import TkConstant from 'TkConstant' ;
import ServiceRoom from 'ServiceRoom' ;
import ServiceSignalling from 'ServiceSignalling' ;
import ServiceTooltip from 'ServiceTooltip' ;
import eventObjectDefine from 'eventObjectDefine';
import CoreController from 'CoreController';

class ControlOverallBarSmart extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            show:{
                allTools:false,   //工具箱的权限
            },
            disabledBlackBoard:false ,
            teachExpansionColumnDisplay:null,
            answerShow:false,
            startAndStop: false,
            restarting:false,
            timeDescArray:[0,5,0,0],
            initArr:[{id:0,"name":"A","sel":false},{id:1,"name":"B","sel":false},{id:2,"name":"C","sel":false},{id:3,"name":"D","sel":false}],
			brr:[],
            crr:[],
            idA:[],
            idB:[],
            idC:[],
            idD:[],
            idE:[],
            idF:[],
            idG:[],
            idH:[],
            allNumbers:0,
            trueLV:0,
            allStudentChosseAnswer:{},
            tableObject:{},
            round:false,
            dialShow:false,
            responderShow:false,
            beginIsStatus:false,
            studentRes:false,
            timerShow:false,
	        userAdmin:"" ,
	        disabledAnswer:false,
	        disabledTurnplate:false,
	        disabledTimer:false,
	        disabledResponder:false,
	        isPublished:false,
            toolsShow:{
                answer:false ,
                turnplate:false ,
                timer:false ,
                responder:false ,
                moreBlackboard:false ,
            },
            teachToolUl:"",
            boxToolClick:false,
        };
        this.listernerBackupid =  new Date().getTime()+'_'+Math.random();
        this.destTopShare = false;
    };
    componentDidMount() { //在完成首次渲染之前调用，此时仍可以修改组件的state
        const that = this ;
        eventObjectDefine.CoreController.addEventListener(TkConstant.EVENTTYPE.RoomEvent.roomPubmsg ,that.handlerRoomPubmsg.bind(that) , that.listernerBackupid ); //roomPubmsg 事件
        eventObjectDefine.CoreController.addEventListener(TkConstant.EVENTTYPE.RoomEvent.roomDelmsg ,that.handlerRoomDelmsg.bind(that) , that.listernerBackupid ); //roomPubmsg 事件
        eventObjectDefine.CoreController.addEventListener(TkConstant.EVENTTYPE.RoomEvent.roomConnected ,that.handlerRoomConnected.bind(that) , that.listernerBackupid ); //roomDisconnected 事件
        eventObjectDefine.CoreController.addEventListener("initAppPermissions" ,that.handlerInitAppPermissions.bind(that) , that.listernerBackupid ); //initAppPermissions：白板可画权限更新
		eventObjectDefine.CoreController.addEventListener("receive-msglist-timer", that.handlerMsglistTimerShow.bind(that), that.listernerBackupid);//倒计时
  	    eventObjectDefine.CoreController.addEventListener( "receive-msglist-dial" , that.handlerMsglistDialShow.bind(that), that.listernerBackupid);//转盘
        eventObjectDefine.CoreController.addEventListener( "receive-msglist-answer" , that.handlerMsglistAnswerShow.bind(that), that.listernerBackupid);//答题卡
		eventObjectDefine.CoreController.addEventListener( "receive-msglist-qiangDaQi" , that.handlerMsglistQiangDaQi.bind(that), that.listernerBackupid);//抢答器
        eventObjectDefine.CoreController.addEventListener('receive-msglist-BlackBoard' ,that.handlerReceive_msglist_BlackBoard.bind(that)  , that.listernerBackupid );
        eventObjectDefine.CoreController.addEventListener( 'callAllWrapOnClick' , that.handlerCallAllWrapOnClick.bind(that) , that.listernerBackupid ); //callAllWrapOnClick 事件-点击整个页面容器
    	eventObjectDefine.CoreController.addEventListener( 'whiteboardToolBox' , that.whiteboardToolBox.bind(that) , that.listernerBackupid ); //白板工具的点击事件
    };
    componentWillUnmount() { //组件被移除之前被调用，可以用于做一些清理工作，在componentDidMount方法中添加的所有任务都需要在该方法中撤销，比如创建的定时器或添加的事件监听器
        const that = this ;
       eventObjectDefine.CoreController.removeBackupListerner(that.listernerBackupid);
    };

    handlerReceive_msglist_BlackBoard(recvEventData){
        this.handlerRoomPubmsg(recvEventData);
        let pubmsgData = recvEventData.message;
        if(pubmsgData.name ===  'BlackBoard'){
            this.setState({
                disabledBlackBoard:true
            });
        }
    };

    /*屏幕的点击事件*/
    handlerCallAllWrapOnClick(recvEventData){
        if(TK.SDKTYPE === 'mobile'){
            let {event} = recvEventData.message ;
            let parentId = this.props.parentId ||  'teach_header_tool_vessel';
            if( !( event.target.id === parentId || ( $(event.target) && $(event.target).parents("#"+parentId).length>0 ) ) ){
              	this.whiteboardToolBox();
            }
        }
    };
    allLiMouseLeave(){
	//  	if(TK.SDKTYPE !== 'mobile'){
	
	//      }
    };
    /*断线重连*/
    handlerRoomConnected(recvEventData){ //房间连接成功后执行的操作
        this.setState({
            disabledBlackBoard:false,
            disabledAnswer:false,
            disabledTurnplate:false,
            disabledTimer:false,
            disabledResponder:false
        });
    };
    /*倒计时的message-list*/
    handlerMsglistTimerShow(recvEventData){
    	for(let message of recvEventData.message.timerShowArr){
    		this.setState({
		            disabledTimer:true
		       });
    	}
    };
    /*转盘的message-list*/
     handlerMsglistDialShow(recvEventData){
     	
     	for(let message of recvEventData.message.dialShowArr){
     		this.setState({
		            disabledTurnplate:true,
		       });
    	}
    };
    /*答题卡的message-list*/
     handlerMsglistAnswerShow(recvEventData){
    	for(let message of recvEventData.message.answerShowArr){
    		this.setState({
		            disabledAnswer:true,
		       });
    	}
    };
    /*抢答器的message-list*/
    handlerMsglistQiangDaQi(recvEventData){
    	for(let message of recvEventData.message.qiangDaQiArr){
    		this.setState({
		            disabledResponder:true,
		       });
    	}
    };
    handlerRoomPubmsg(recvEventData){
        let pubmsgData = recvEventData.message;
        switch (pubmsgData.name) {
            case "BlackBoard":
                this.setState({
                    disabledBlackBoard:true
                });
                break;
            case "answer":
                this.setState({
                   disabledAnswer:true
                });
                break;
            case "dial":
	            this.setState({
	               disabledTurnplate:true
	            });
	            break;
            case "timer":
	            this.setState({
	               disabledTimer:true
	            });
	            break;
	        case "qiangDaQi":
	            this.setState({
	               disabledResponder:true
	            });
	            break;    
        }
    };
    handlerRoomDelmsg(recvEventData){
        let delmsgData = recvEventData.message;
        switch (delmsgData.name) {
            case "BlackBoard":
                this.setState({
                    disabledBlackBoard:false
                });
                break;
            case "answer":
                this.setState({
                   disabledAnswer:false
                });
                break;
            case "dial":
	            this.setState({
	               disabledTurnplate:false
	            });
	            break;
            case "timer":
	            this.setState({
	               disabledTimer:false
	            });
	            break;
	        case "qiangDaQi":
	            this.setState({
	               disabledResponder:false
	            });
	            break;
            case "ClassBegin":
                this.setState({
                    disabledAnswer:false,
                    disabledTurnplate:false,
                    disabledTimer:false,
                    disabledResponder:false,
                    disabledBlackBoard:false
                });
        }
    };
	/*初始化权限*/
    handlerInitAppPermissions(){
        let toolsShow = {
            answer:true ,
            turnplate:true ,
            timer:true ,
            responder:true ,
            moreBlackboard:true ,
        };
        Object.assign(this.state.toolsShow , toolsShow);
        this.state.show.allTools = CoreController.handler.getAppPermissions('allUserTools');
        this.setState({show:this.state.show , destTopShare:this.state.toolsShow});
    };
    /*显示教学工具箱*/
    teachExpansionController(e){
    	e.preventDefault();
		e.stopPropagation();
		this.state.boxToolClick=!this.state.boxToolClick;
		this.setState({
        	boxToolClick: this.state.boxToolClick,
        });
        if(this.state.boxToolClick){
        	let boxID=document.getElementById("tool-box");
	    	boxID.setAttribute("class","tool-box-wrap-active");
	        this.state.teachExpansionColumnDisplay = "block";
	        this.state.teachToolUl= "block";
	        this.setState({
	        	teachExpansionColumnDisplay: this.state.teachExpansionColumnDisplay,
	        	teachToolUl:this.state.teachToolUl
	        });	
	        eventObjectDefine.CoreController.dispatchEvent({
	                        type:'teacherToolBox' ,
	        });
        }else{
        	this.teachBoxLeftMouseOut();
        }
    	
    };
    /*监听其他工具的点击事件*/
   	whiteboardToolBox(){
   		this.teachBoxLeftMouseOut();
    };
    /*隐藏工具的事件*/
    teachBoxLeftMouseOut(){
        this.state.boxToolClick=false;
        this.setState({
            boxToolClick: this.state.boxToolClick
        });
    	let boxID=document.getElementById("tool-box");
    	boxID.setAttribute("class","tool-box-wrap");
        this.state.teachExpansionColumnDisplay = "none";
        this.setState({teachExpansionColumnDisplay: this.state.teachExpansionColumnDisplay});
    };
    /*答题器组件的显示的控制*/
    answerAssemblyShowHandel(e){
    	if(this.state.disabledAnswer){
    		return false;
    	}
    	e.preventDefault();
		e.stopPropagation();
    	this.state.teachToolUl = "none";
    	this.state.answerShow=true;
    	this.setState({
    		answerShow:this.state.answerShow,
    		teachToolUl:this.state.teachToolUl
    	})
    	this.teachBoxLeftMouseOut();
    	let rounds = this.state.round;
        let optionalAnswer=this.state.initArr;
        let studentSels = this.state.brr;
        let trueLV= this.state.trueLV;
    	let allNumbers= this.state.allNumbers;
    	let dataChoose = this.state.allStudentChosseAnswer;
    	let idAS = this.state.idA;
		let idBS = this.state.idB;
		let idCS = this.state.idC;						
		let idDS = this.state.idD;
		let idES = this.state.idE;					
		let idFS = this.state.idF;				
		let idGS = this.state.idG;					
		let idHS = this.state.idH;
		let dataTable = this.state.tableObject;
		let isPublished = this.state.isPublished
        let quizTime=document.getElementById("result-teach-mytime").textContent;
        let newCrr=Array.from(new Set(this.state.crr));
        	newCrr=newCrr.sort();
    	let iconShow=this.state.answerShow;
    	let data = {
            optionalAnswers:optionalAnswer,
            quizTimes:quizTime,
            rightAnswers:newCrr,
            isRound:rounds,
            studentSelect:studentSels,
            trueLV:trueLV,
            allNumbers:allNumbers,
            dataChoose:dataChoose,
            dataTable:dataTable,
            isPublished:isPublished,
            idAS:idAS,
            idBS:idBS,
            idCS:idCS,
            idDS:idDS,
            idES:idES,
            idFS:idFS,
            idGS:idGS,
            idHS:idHS,
            isShow:iconShow
        	} 
        	ServiceSignalling.sendSignallingAnswerToStudent(data);
    	 	eventObjectDefine.CoreController.dispatchEvent({
                        type:'handleAnswerShow' ,
                        className:"answer-implement-bg",
                        isShow:iconShow,
               });
    };
    /*转盘组件的显示的控制*/
    turntableAssemblyShowHandel(e){
    	if(this.state.disabledTurnplate){
    		return false;
    	}
    	e.preventDefault();
		e.stopPropagation();
    	this.state.teachToolUl = "none";
    	this.state.dialShow =true;
    	this.setState({
    		dialShow:this.state.dialShow,
    		teachToolUl:this.state.teachToolUl
    	})
    	this.teachBoxLeftMouseOut();
        let i=0;
        let iconShow=this.state.dialShow
    	let data={
                rotationAngle:'rotate('+ i * 900 +'deg)',
                isShow:iconShow
			};
		let isDelMsg = false;
    	ServiceSignalling.sendSignallingDialToStudent(data);
    };
    /*倒计时组件的显示的控制*/
    timerAssemblyShowHandel(e){
    	if(this.state.disabledTimer){
    		return false;
    	}
    	e.preventDefault();
		e.stopPropagation();
    	this.state.teachToolUl = "none";
    	this.state.timerShow=true;
    	this.state.restarting=false;
    	this.setState({
    		timerShow:this.state.timerShow,
    		restarting:this.state.restarting,
    		teachToolUl:this.state.teachToolUl
    	});
    	this.teachBoxLeftMouseOut();
    	let stopBtn= this.state.startAndStop;
    	let dataTimerArry= this.state.timeDescArray;
    	let iconShow=this.state.timerShow;
    	let isRestart = this.state.restarting;
		let data = {
				isStatus: stopBtn,
				sutdentTimerArry: dataTimerArry,
				isShow:iconShow,
				isRestart:isRestart
		};
		ServiceSignalling.sendSignallingTimerToStudent(data);
    	eventObjectDefine.CoreController.dispatchEvent({
                        type:'handleTurnShow' ,
                        className:"timer-implement-bg",
                        isShow:iconShow,
                 });
    };
    /*抢答器的显示的控制*/
	responderAssemblyShowHandel(e){
		const that = this;
		if(that.state.disabledResponder){
			return false;
		};
		e.preventDefault();
		e.stopPropagation();
		that.state.teachToolUl = "none";
		that.state.responderShow = true;
		that.setState({
			responderShow: that.state.responderShow,
			teachToolUl:that.state.teachToolUl
		});
		this.teachBoxLeftMouseOut();
		let iconShow = that.state.responderShow;
		let begin = that.state.beginIsStatus;
		let userAdmin = this.state.userAdmin
		let data = {
			isShow: iconShow,
			begin: begin,
			userAdmin: userAdmin
		}
		let isDelMsg = false;
		ServiceSignalling.sendSignallingQiangDaQi(isDelMsg, data);
		eventObjectDefine.CoreController.dispatchEvent({
			type: 'responderShow',
			className: "responder-implement-bg",
			isShow: iconShow,
		});
		
	};

    handleBlackBoardClick(){
        let isDelMsg = false , data = {
            blackBoardState:'_prepareing' ,  //_none:无 , _prepareing:准备中 , _dispenseed:分发 , _recycle:回收分发 , _againDispenseed:再次分发
            currentTapKey:'blackBoardCommon' ,
            currentTapPage:1 ,
        } ;
        ServiceSignalling.sendSignallingFromBlackBoard(data , isDelMsg);
    };

    render(){
        let that = this;
        let {show  , onClick , toolsShow } = that.state ;
        let {allTools} = show ;
        let toolsNum =  0 ; //显示工具包的个数
        for(let value of Object.values(toolsShow) ){
            if(value){
                toolsNum++;
            }
            if(toolsNum > 5){
                toolsNum = 5 ;
                break ;
            }
        }
        return (
	       <ol className="all-participant-function add-fl clear-float  h-tool"  id="teach_header_tool_vessel" style={{display:allTools?'block':'none'}}> {/*全员操作功能组件*/}
	            <li   className="tl-all-participant-tools " id="tl-all-participant-tools-id" title={TkGlobal.language.languageData.toolCase.toolBox.text} onMouseLeave={that.allLiMouseLeave.bind(this) } onTouchStart={this.teachExpansionController.bind(this)}  >
		            <button >
                        <span className="tool-box-wrap" id="tool-box"></span>
                    </button>
	                <div className={"teach-expansion-column "} style={{display:this.state.teachExpansionColumnDisplay , width:'calc(100% * '+toolsNum+')'}} >
	                    <ul className="teach-box-left clear-float"   ref="teachBoxLeftStyle" style={{display:this.state.teachToolUl}}>
	                        <li className={"answer-implement-bg"+" " +(this.state.disabledAnswer?'disabled':'') }  disabled={this.state.disabledAnswer} onTouchStart={this.answerAssemblyShowHandel.bind(this)}  style={{display:!toolsShow.answer?'none':''}} ></li>
	                        <li className={"turnplate-implement-bg"+" "+ (this.state.disabledTurnplate?'disabled':'') }  disabled={this.state.disabledTurnplate} onTouchStart={this.turntableAssemblyShowHandel.bind(this)} style={{display:!toolsShow.turnplate?'none':''}}  ></li>
	                        <li className={"timer-implement-bg"+" "+ (this.state.disabledTimer?'disabled':'') }  disabled={this.state.disabledTimer} onTouchStart={this.timerAssemblyShowHandel.bind(this)} style={{display:!toolsShow.timer?'none':''}}  ></li>
	                        <li className={"responder-implement-bg"+ " "+ (this.state.disabledResponder?'disabled':'') } disabled={this.state.disabledResponder} onTouchStart={this.responderAssemblyShowHandel.bind(this)}  style={{display:toolsShow.responder ?'':'none'}} ></li>
	                    	<li className={"tl-more-black-board "+ (this.state.disabledBlackBoard?'disabled':'') }  title={TkGlobal.language.languageData.header.tool.blackBoard.title.open} disabled={this.state.disabledBlackBoard} id="more_black_board"  style={{display:!toolsShow.moreBlackboard?'none':''}}  onTouchStart={this.handleBlackBoardClick.bind(this)} ></li>
						</ul>
	                </div>
	            </li>
	        </ol>
        )
    };

};
export default  ControlOverallBarSmart;




